<template>
  <div>
    demo2<input type="text" v-model="demo2">
    <button @click="aaa">111</button>
    <button @click="bbb">222</button>
  </div>
</template>

<script>
  import Bus from '../../assets/api/bus.js'

  export default {
    name: 'demo2',
    data(){
      return{
        demo2:''
      }
    },
    methods:{
      aaa(){
        Bus.$on('val',(data) => {
          this.demo2 = data
        })
      },
      bbb(){
        Bus.$off('val')
      }
    },
  }
</script>

